*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --title-color: #fff;
  --main-text-color: #000;
  --second-text-color: #ff6b08;
  --card-info-text-color: #8c8c8c --main-bg-color: #fff;
  --footer-bg-color: #f7f7f7;
  --button-color: #ff6b01;
  --animation-timing-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  letter-spacing: .03em;
  color: var(--main-text-color);
  background-color: var(--main-bg-color);
  font-family: Roboto, sans-serif;
  font-size: 20px;
  font-weight: 500;
}

body::-webkit-scrollbar {
  width: 21px;
}

body::-webkit-scrollbar-track {
  background-color: #564d4d;
}

body::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, #302d2d, #ff001b);
  border-radius: 5px;
}

*, p, h1, h2, h3, h4, h6 li {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

main {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.top_button {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  background-color: tomato;
  border: none;
  border-radius: 50%;
  outline: none;
  padding: 10px;
  font-size: 18px;
  transition: all .5s;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

.top_button:hover, .top_button:focus {
  background-color: red;
}

html {
  scroll-behavior: smooth;
}

.loader {
  width: 30px;
  height: 30px;
  border: 5px solid #fff;
  border-top-color: tomato;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 1s linear infinite;
  display: block;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-hidden {
  display: none;
}

@keyframes spin {
  from {
    transform: translate(-50%)rotate(0deg);
  }

  to {
    transform: translate(-50%)rotate(360deg);
  }
}

.preloader {
  z-index: 1001;
  background: #e0e0e0;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.preloader__row {
  width: 70px;
  height: 70px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 50%;
  left: 50%;
}

.preloader__item {
  width: 35px;
  height: 35px;
  background-color: tomato;
  border-radius: 100%;
  animation: preloader-bounce 3s ease-in-out infinite;
  display: inline-block;
  position: absolute;
  top: 0;
}

.preloader__item:last-child {
  animation-delay: -1s;
  top: auto;
  bottom: 0;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity .3s;
}

.loaded .preloader {
  display: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
