.backdrop {
  z-index: 999;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, .56);
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  position: fixed;
  top: 0;
}

.backdrop.show-modal .modal {
  transform: translate(-50%, -50%)scale(1);
}

