  width: calc(100% - 30px);
  max-width: 290px;
  background-color: #fff;
  padding: 35px 15px 30px;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1.1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
